.AreaChart {
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.AreaChart svg .area.upa {
  fill: var(--upa--color);
}

.AreaChart svg .area.mahagathbandhan-mayawati {
  fill:  var(--mahagathbandhan-mayawati--color);
}

.AreaChart svg .area.nda {
  fill: var(--nda--color);
}