body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --upa--color: rgb(227, 227, 255); 
  --mahagathbandhan-mayawati--color: rgb(135, 218, 134);
  --nda--color: rgb(252, 185, 98);

}


.AreaChart {
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.AreaChart svg .area.upa {
  fill: var(--upa--color);
}

.AreaChart svg .area.mahagathbandhan-mayawati {
  fill:  var(--mahagathbandhan-mayawati--color);
}

.AreaChart svg .area.nda {
  fill: var(--nda--color);
}
.OverviewAreaChart figure {
    /* background: #333; */
    margin: 0;
    padding: 50px 0;
}

.OverviewAreaChart .OverviewAreaChart--wrapper {
    height: 290px;
    position: relative;
    width: 100%;
}


.StreamGraph {
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.StreamGraph svg .line {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .25);
  stroke-dasharray: 1 2;
  stroke-width: 1px;
}

.StreamGraph svg .bullet {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .25);
  stroke-width: 1px;
}

.StreamGraph svg .label {
  fill: rgba(0, 0, 0, .75);
  font-size: 10px; 
}

.StreamGraph svg .area.upa {
  fill: var(--upa--color);
}

.StreamGraph svg .area.mahagathbandhan-mayawati {
  fill:  var(--mahagathbandhan-mayawati--color);
}

.StreamGraph svg .area.nda {
  fill: var(--nda--color);
}
.OverviewSteamGraph figure {
    /* background: #333; */
    margin: 0;
    padding: 50px 0;
}

.OverviewSteamGraph .OverviewSteamGraph--wrapper {
    height: 290px;
    position: relative;
    width: 100%;
}


.WaveChart {
  display: block;
  height: 100%;
  position: relative;
  width: 100%;
}

.WaveChart .WaveChart--chart {
  bottom: 80px;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.WaveChart .WaveChart--Articles {}

.WaveChart .WaveChart--Articles article {
  bottom: 5px;
  font-size: 10px;
  height: 75px;
  max-width: 100px;
  position: absolute;
  text-align: center;
  -webkit-transform: translate3d(-50%,0,0);
          transform: translate3d(-50%,0,0);
}

.WaveChart .WaveChart--Articles article a {
  color: rgba(0, 0, 0, .75);
  text-decoration: none;
}

.WaveChart svg .area.upa {
  fill: var(--upa--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.mahagathbandhan-mayawati {
  fill: var(--mahagathbandhan-mayawati--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.nda {
  fill: var(--nda--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.nda-popularity,
.WaveChart svg .area.mahagathbandhan-mayawati-popularity,
.WaveChart svg .area.upa-popularity {
  fill: rgba(0, 0, 0, .15);
  mix-blend-mode: multiply;
}

.WaveChart svg .line {
  stroke: rgba(0, 0, 0, .25);
  stroke-dasharray: 1 2;
  stroke-width: 1px;
}

.WaveChart svg .label {
  fill: rgba(0, 0, 0, .5);
  font-size: 10px;  
}

.WaveChart svg .circle {
  fill: rgba(0, 0, 0, .5);
  stroke: transparent;
  stroke-width: 0;
}
.Timeline figure {
    /* background: #333; */
    margin: 0;
    padding: 50px 0;
}

.Timeline .Timeline--wrapper {
    height: 440px;
    position: relative;
    width: 100%;
}


.LineChart {
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.LineChart svg .line {
  fill: none;
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .15);
  stroke-dasharray: none;
  stroke-width: 1px;
}

.LineChart svg .line.selected {
  fill: none;
  stroke: rgba(190, 0, 90, .75);
  stroke-dasharray: none;
  stroke-width: 2px;
}

.LineChart svg .xAxis {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .75);
  stroke-dasharray: none;
  stroke-width: 1px;
}

.LineChart svg .dateLine {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .25);
  stroke-dasharray: 1 2;
  stroke-width: 1px;
}

.LineChart svg .label {
  fill: rgba(0, 0, 0, .75);
  font-size: 10px; 
}



.LineChart svg .bullet {
  mix-blend-mode: multiply;
  fill: rgba(0, 0, 0, .25);
}
.CompareCandidates {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    margin: 0;
}

.CompareCandidates .CompareCandidates--candidate {
    display: block;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding: 0 10px;
    position: relative;
}

.CompareCandidates .CompareCandidates--chart {
    height: 150px;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
}



