.LineChart {
  bottom: 0;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.LineChart svg .line {
  fill: none;
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .15);
  stroke-dasharray: none;
  stroke-width: 1px;
}

.LineChart svg .line.selected {
  fill: none;
  stroke: rgba(190, 0, 90, .75);
  stroke-dasharray: none;
  stroke-width: 2px;
}

.LineChart svg .xAxis {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .75);
  stroke-dasharray: none;
  stroke-width: 1px;
}

.LineChart svg .dateLine {
  mix-blend-mode: multiply;
  stroke: rgba(0, 0, 0, .25);
  stroke-dasharray: 1 2;
  stroke-width: 1px;
}

.LineChart svg .label {
  fill: rgba(0, 0, 0, .75);
  font-size: 10px; 
}



.LineChart svg .bullet {
  mix-blend-mode: multiply;
  fill: rgba(0, 0, 0, .25);
}