.WaveChart {
  display: block;
  height: 100%;
  position: relative;
  width: 100%;
}

.WaveChart .WaveChart--chart {
  bottom: 80px;
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.WaveChart .WaveChart--Articles {}

.WaveChart .WaveChart--Articles article {
  bottom: 5px;
  font-size: 10px;
  height: 75px;
  max-width: 100px;
  position: absolute;
  text-align: center;
  transform: translate3d(-50%,0,0);
}

.WaveChart .WaveChart--Articles article a {
  color: rgba(0, 0, 0, .75);
  text-decoration: none;
}

.WaveChart svg .area.upa {
  fill: var(--upa--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.mahagathbandhan-mayawati {
  fill: var(--mahagathbandhan-mayawati--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.nda {
  fill: var(--nda--color);
  mix-blend-mode: multiply;
}

.WaveChart svg .area.nda-popularity,
.WaveChart svg .area.mahagathbandhan-mayawati-popularity,
.WaveChart svg .area.upa-popularity {
  fill: rgba(0, 0, 0, .15);
  mix-blend-mode: multiply;
}

.WaveChart svg .line {
  stroke: rgba(0, 0, 0, .25);
  stroke-dasharray: 1 2;
  stroke-width: 1px;
}

.WaveChart svg .label {
  fill: rgba(0, 0, 0, .5);
  font-size: 10px;  
}

.WaveChart svg .circle {
  fill: rgba(0, 0, 0, .5);
  stroke: transparent;
  stroke-width: 0;
}